import request from '@/utils/request'
export function fetchList(params) {
  return request({
    url:'/us/stocksBase/listAll',
    method:'get',
    params:params
  })
}

export function fetchOne(params) {
  return request({
    url:'/us/historyKl/list',
    method:'get',
    params:params
  })
}

export function subscribeStock(params) {
  return request({
    url:'/us/historyKl/subscribe',
    method:'get',
    params:params
  })
}

export function unSubscribeStock(params) {
  return request({
    url:'/us/historyKl/unsubscribe',
    method:'get',
    params:params
  })
}

export function pingStatus(params) {
  return request({
    url:'/us/stocksBase/ping-status',
    method:'get',
    params:params
  })
}




